.container{
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    overflow-y: auto;
    margin-bottom: 5px;
}

.container img{
    border-radius: 99%;
    height: 40px;
    width: 40px;
}


.messageContainer{
    display: flex;

    box-sizing: border-box;
    text-align: left;
    background-color: rgb(54, 54, 54);
    width: 100%;
    padding: 10px;
    margin-top: 2px;
}

.messageData{
    display: flex;
    flex-direction: column;
    margin-top: 5px;
    margin-left: 10px;
}

#message{
    display: block;
}