.container{
    width: 99%;
    padding-bottom: 15px;
    padding-top: 15px;

}
.container form{
    display: flex;
}
.container input{
    width: 100%;
    padding:8px;
    background-color: #3A3845;
    color: white;
    border: none;
    outline: none;
    font-size: 1.2em;

}

.container input:focus{
    border: none;
}

.container button{
    padding: 10px 20px;
    color: white;
    background-color: #3A3845;
    border: none;
    border-left: 2px solid #2B2B2B;
    cursor: pointer;
}