.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    color: white;
    width: 100%;
}

.container span{
    margin-top: 20px;
    display: block;
}


.container #directions{
    color: red;
}
.container #taunt{
    margin-top: 0px;
}
.container input{
    width: 90%;
    max-width: 500px;
    padding:8px;
    background-color: #3A3845;
    color: white;
    border: none;
    outline: none;
    font-size: 1.2em;

    margin-top: 30px;
}

.container a{
    text-decoration: none;
    color: aqua;
    margin-top: 10px;
}


.container .newMethod{
    margin: 0;
    color: red;
}
