.container{
    background-color: #423F3E;
    width: 20%;
    min-width: 250px;
    max-width: 300px;
    overflow-x: hidden;
}

.container .header{
    display: flex;

}

.container .header img{
    border-right: 2px solid black;
    margin-right: 3vw;
    padding: 10px 10px;
    cursor: pointer;

}

.container .header span{
    padding: 10px 10px;
}

.item{
    padding: 10px 10px;
    text-align: left;
    display: flex;
}

.item:hover{
    background-color: #2B2B2B;
    cursor: pointer;
}

.item .avatar{
    border-radius: 99%;
    width: 45px;
    margin-right: 10px;
}

.item .username span{
}

.item .username span:first-child{
    display: block;
    color: gold;

}

.item .username span:last-child{
    display: block;
    color: rgb(208, 208, 208);
    font-size: 0.7em;
}



@media only screen and (max-width: 665px) {
    .container{
        position: absolute;
        left: 0;
     
        height: 100%;
        max-width: 100%;
        width: 100%;
       
    }
  
  
  
  }


.containerCollapse{
    width: 40px;
    padding-top: 10px;
    background-color: #1e1e1e;
}

.containerCollapse .header{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.containerCollapse img{
    cursor: pointer;
    width: 25px;

    margin-top: 40px;
}

.containerCollapse img:hover{
    cursor: pointer;
    background-color: rgb(76, 76, 76);
}